import tuiActionsheet from "@/components/actionsheet/actionsheet"
import {
	api,
	postajax
} from '@/utils/api.js'
import {
	fileUrl
} from '@/utils/index.js'
export default {
	data() {
		return {
			userinfo: {},
			stuinfo: {},
			showActionSheet: false,
			tips: '退出登录会清除您的登录信息，确认退出吗？',
			itemList: [{
				text: "退出登录",
				color: "#e53a37"
			}],
			avatarUrl: '',
			menulist: [{
				'name': '积分',
				icon: '/static/icon9.png'
			}, {
				'name': '设置',
				icon: '/static/icon10.png'
			}, {
				'name': '修改密码',
				icon: '/static/icon11.png'
			}, {
				'name': '关于家校通',
				icon: '/static/icon12.png'
			}]
		};
	},
	components: {
		tuiActionsheet
	},
	onShow() {
		this.init();
	},
	onPullDownRefresh() {
		this.init();
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 500);
	},
	methods: {
		init() {
			this.stuinfo = uni.getStorageSync('stuinfo') //关联学生信息
			this.userinfo = uni.getStorageSync('userinfo') //账户信息
			this.avatarUrl = fileUrl + this.userinfo.picUrl;
		},
		openActionSheet() {
			setTimeout(() => {
				this.showActionSheet = true;

			}, 0)
		},
		itemClick: function(e) {
			let index = e.index;
			this.closeActionSheet();
			/* 退出登录 */
			this.logout();

		},
		logout() {
			postajax(api.logout).then((da) => {
				if (da.code == 0) {
					uni.clearStorageSync();
					uni.reLaunch({
						url: '/pages/login/login'
					})

				}
			})
		},
		closeActionSheet: function() {
			this.showActionSheet = false
		},
		showDetails() {
			uni.navigateTo({
				url: '/pages/editUserinfo/editUserinfo'
			})
		},
		gotopage(){
			uni.showToast({
				title:'敬请期待',
				icon:'none'
			})
		}
	}
}
